import {required, minLength, maxLength} from 'vuelidate/lib/validators'

export default {
  name: "CreateSchool",
  data: () => ({
    loading: true,
    INN: null,
    shortname: null,
    district: null,
    districts: []
  }),
  validations: {
    INN: {
      required,
      minLength: minLength(10),
      maxLength: maxLength(12)
    },
    shortname: {
      required
    },
    district: {
      required
    }
  },
  async mounted() {
    try {
      const resp = await this.$store.dispatch('fetchDistricts')
      for (let key in resp) {
        this.districts.push(resp[key].name.name)
      }
      this.loading = false
    } catch (e) {
      console.log(e)
    }
  },

  methods: {
    async clickHandler() {
      try {
        if (this.$v.$invalid) {
          this.$v.$touch()
          return
        }
        const dataForm = {
          INN: this.INN,
          shortname: this.shortname,
          district: this.district
        }
        console.log(dataForm)
        if (dataForm.district && dataForm.shortname && dataForm.INN) {
          await this.$store.dispatch('createSchool', dataForm)
          await this.$router.push('/districts')
        } else {
          alert('Введите все поля!')
        }
      } catch (e) {
        console.log(e)
      }
    }
  }
}
